var config = require('./appconfig.json');
import fs = require('fs');

var args = process.argv.slice(2);


var cfg = config;
cfg.watchPaths.cs = args[0];
cfg.watchPaths.ts = args[1];

var newConfig = JSON.stringify(cfg);
fs.writeFile('./src/appconfig.json', newConfig, 'utf-8', function (err) {
    if (err) {
        console.log('failed to save ' + err);
    } else {
        console.log("succesfully saved the new config.");
        console.log("CS folder path: " + cfg.watchPaths.cs);
        console.log("TS folder path: " + cfg.watchPaths.ts);
    }
});
